<template>
  <div class="recommend">
    <div class="recommend-hot">
      <!-- <div class="recommend-hot-title">
        <div>热门歌手</div>
        <div>查看更多</div>
      </div> -->
      <div>
        <van-tabs
          v-model="active"
          line-height="0"
          background="#272727"
          title-active-color="#d4237a"
          animated
          swipeable
        >
          <van-tab
            v-for="(item, index) in recommendTypeData"
            :title="item.title"
            :key="index"
            class="recommend-box"
          >
            <div
              class="recommend-item"
              v-for="(item, ind) in recommendData"
              :key="ind"
              @click="godetail(item)"
            >
              <div class="recommend-img-box">
                <img class="auto-img" :src="item.picUrl" />
              </div>
              <div class="recommend-content">
                <div class="recommend-name fontnowrap">{{ item.name }}</div>
                <div v-show="active == 0" class="recommend-text">
                  <div class="album">专辑 {{ item.albumSize }}</div>
                  <div class="single">单曲：{{ item.musicSize }}</div>
                </div>
                <div v-show="active == 2" class="recommend-text clearfix">
                  <div class="album fl fontnowrap">{{ item.albumSize }}</div>
                  <div class="single fr singlecount">
                    <van-icon name="music-o" />{{ item.musicSize | million }}
                  </div>
                </div>
                <div v-show="item.artistName" class="recommend-text">
                  <div class="album">{{ item.artistName }}</div>
                </div>
              </div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </div>
    <div class="recommend-hot">
      <div class="hot-title">新碟推荐</div>
      <van-swipe :loop="false" :show-indicators="false" :width="90">
        <van-swipe-item
          v-for="(item, index) in HotData"
          :key="index"
          class="hotdata-item"
          @click="goSongListDetails(item)"
        >
          <img class="auto-img hotdata-item-img" :src="item.picUrl" alt="" />
          <div class="hotdata-item-name">{{ item.name }}</div>
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="recommend-comment">
      <div class="hot-title">云村热评</div>
      <van-swipe
        :autoplay="5000"
        duration=1500
        :show-indicators="false"
        style="height: 120px"
        vertical
      >
        <van-swipe-item
          class="comment-item clearfix"
          v-for="(item, index) in HotComment"
          :key="index"
        >
          <div class="fl comment-item-left">
            <img
              class="auto-img comment-item-songimg"
              :src="item.simpleResourceInfo.songCoverUrl"
            />
            <div class="comment-item-songname">
              {{ item.simpleResourceInfo.name }}
            </div>
          </div>
          <div class="fl comment-item-right">
            <div class="comment-item-right-t">
              <div class="comment-item-userimg">
                <img
                  class="auto-img"
                  :src="item.simpleUserInfo.avatar"
                  alt=""
                />
              </div>
              <div class="comment-item-nickname">
                {{ item.simpleUserInfo.nickname }}
              </div>
            </div>
            <div class="comment-item-content">{{ item.content }}</div>
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="recommend-list">
      <div class="hot-title">热门榜单</div>
      <div class="list-box">
        <van-grid square :border="false" :column-num="4">
          <van-grid-item v-for="(item,index) in TopList" :key="index">
            <img class="auto-img" :src="item.coverImgUrl" :alt="item.name">
          </van-grid-item>
        </van-grid>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/recommend.less";
import { setupCookie } from "../../assets/js/setupCookie";
export default {
  data() {
    return {
      active: "",
      recommendData: [],
      HotData: [],
      HotComment: [],
      TopList:[],
      recommendTypeData: [
        {
          title: "热门歌手",
        },
        {
          title: "热门歌曲",
        },
        {
          title: "热门专辑",
        },
        {
          title: "热门MV",
        },
      ],
    };
  },
  filters: {
    million: function (value) {
      //播放量数值处理
      let num;
      if (value > 99999999) {
        num = Math.floor(value / 10000000) / 10 + "亿";
      } else if (value > 9999) {
        num = Math.floor(value / 1000) / 10 + "万";
      } else if (value < 9999 && value > -9999) {
        num = value;
      }
      return num;
    },
  },
  created() {
    this.getTopList();
    this.getNewAlbum();
    this.getHotComment();
  },
  watch: {
    active(val) {
      if (val == 0) {
        this.getHotData("/top/artists", { limit: 9 });
      } else if (val == 1) {
        this.getHotData("/recommend/songs", {
          cookie: setupCookie.getCookies(),
        });
      } else if (val == 2) {
        this.getHotData("/personalized", { limit: 9 });
      } else {
        this.getHotData("/mv/first", { limit: 9 });
      }
    },
  },
  methods: {
    //获取热门分类
    getHotData(url, data) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: url,
        params: data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code === 200) {
            if (this.active == 0) {
              this.recommendData = result.data.artists.slice(0, 8).map((v) => {
                return {
                  id:v.id,
                  picUrl: v.img1v1Url,
                  name: v.name,
                  musicSize: v.musicSize,
                  albumSize: v.albumSize,
                };
              });
            } else if (this.active == 1) {
              this.recommendData = result.data.data.dailySongs
                .slice(0, 8)
                .map((v) => {
                  return {
                    id:v.id,
                    picUrl: v.al.picUrl,
                    name: v.al.name,
                  };
                });
            } else if (this.active == 2) {
              this.recommendData = result.data.result.slice(0, 8).map((v) => {
                return {
                  id:v.id,
                  picUrl: v.picUrl,
                  name: v.name,
                  musicSize: v.playCount,
                  albumSize: v.copywriter,
                };
              });
            } else {
              this.recommendData = result.data.data.slice(0, 8).map((v) => {
                return {
                  picUrl: v.cover,
                  name: v.name,
                  artistName: v.artistName,
                };
              });
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.error(err)
        });
    },
    //新碟推送
    getNewAlbum() {
      this.axios({
        method: "GET",
        url: "/album/new",
        params: {
          limit: 9,
        },
      })
        .then((result) => {
          if (result.data.code === 200) {
            this.HotData = result.data.albums;
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
    //获取热评
    getHotComment() {
      this.axios({
        method: "GET",
        url: "/comment/hotwall/list",
      })
        .then((result) => {
          if (result.data.code === 200) {
            this.HotComment = result.data.data;
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
    //热门榜单
    getTopList() {
      this.axios({
        method: "GET",
        url: "/toplist",
      })
        .then((result) => {
          if (result.data.code === 200) {
            this.TopList = result.data.list.slice(0,8)
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
    //前往详情
    godetail(item){
      if (this.active == 0) {
        this.$router.push({path:'singerDetail',query:{id:item.id}})
      }else if (this.active == 1) {
        this.playmusicids = this.recommendData.map(v => {
        return v.id
      })
      this.$store.commit("setplaymusicids",this.playmusicids)
      this.$store.commit("setmusicid",item.id)
      }else if (this.active == 2) {
        //前往歌单详情页面
        this.$router.push({path:'songListDetails', query:{id:item.id}})
      }
    },
    //前往歌单详情页面
    goSongListDetails(item){
      this.$router.push({path:'songAlbumDetails', query:{id:item.id}})
    },
  },
};
</script>
